.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border: 2px solid green;
  border-radius: 5px;
  padding: 10px;
  text-align: center;
  margin-bottom: 10px;
}

.box {
  color: aqua;
  font-size: 20px;
  border: 2px solid grey;
  border-radius: 6px;
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
  margin-bottom: 10px;
  width: 50%;
  margin: 0 auto;
}
